.App{
    .sidebar{
        width: 16%;
        height: 100vh;
        background-color: #2f3136;
        position:fixed;
        z-index: 11;
        transition: all .5s ease-in-out;
        @media screen and (max-width: 1100px) {
            transform: translateX(-100%);
            width: 35%;
        }
        @media screen and (max-width: 411px) {
            width: 50%;
        }
    }
    .nav-toggle{
        @media  screen and (max-width: 1100px) {
            transform: translateX(0);
        }
    }

    .main-content{
        width: 84%;
        margin-left: 16%;
        background-color: #36393f;
        min-height: 100vh;
        display: grid;
        position: relative;
        .content{
            margin: 4rem;
            text-align: center;
        }
        @media screen and (max-width: 1100px){
            margin-left: 0;
            width: 100%;
        }
    }
}
.nav-botton{
    position: fixed;
    z-index: 10;
    right: 5%;
    top: 5%;
    width: 3rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    .lines-1, .lines-2, .lines-3{
        height: .4rem;
        width: 100%;
        background-color: #008cff;
        pointer-events: none;
        display: none;
        border-radius: 20px;
        &:not(:last-child){
            margin-bottom: .5rem;
        }
    }
    @media screen and (max-width: 1100px){
        display: block;
        .lines-1, .lines-2, .lines-3{
            display: block;
        }
    }
}